import React from "react";
import mainBody from "../../css/main/MainBody.module.css";

import Container from "react-bootstrap/esm/Container";

import NavigateBtn from "./NavigateBtn";

import MainScreen from "./1/MainScreen";
import BestFlirdog from "./2/BestFlirdog";
import SmallGroupMain from "./3/SmallGroupMain";
import CommunityMain from "./4/CommunityMain";
import BestProductMain from "./5/BestProductMain";

const MainBody = () => {
  return (
    <>
      <MainScreen></MainScreen>

      <Container className="px-10">
        <BestFlirdog />
      </Container>
      <SmallGroupMain></SmallGroupMain>
      <CommunityMain></CommunityMain>
      <Container className="px-10">
        <BestProductMain></BestProductMain>

        <div className="mt-7 d-flex justify-content-center align-items-center">
          <NavigateBtn
            text=" "
            url="/product"
            fontSize="2.5vw"
            btnWidth="25vw"
            btnHeight="80px"
          ></NavigateBtn>
        </div>
      </Container>
    </>
  );
};

export default MainBody;
