import React from "react";
import LeftSide from "../LeftSide";
import AdminHeader from "../AdminHeader";
import styles from "../../../css/admin/rightContent.module.css";
import RightContentHeader from "../RightContentHeader";
import ProductDetail from "./1/ProductDetail";
import EditorBox from "../../EditorBox";
import Container from "react-bootstrap/esm/Container";
import AdminPageInfoText from "../AdminPageInfoText";
import ProductPrimaryInfo from "./2/ProductPrimaryInfo";
import ProductPrice from "./3/ProductPrice";
import Alert from "react-bootstrap/Alert";
import ProductStock from "./4/ProductStock";
import ProductNoticeBanner from "./ProductNoticeBanner";
import ProductThumnail from "./5/ProductThumnail";

const ProductUploadForm = ({ openLeftside }) => {
  return (
    <>
      <AdminHeader></AdminHeader>

      <LeftSide openLeftside={openLeftside} selected=" "></LeftSide>
      <div className={styles.rightContent}>
        <Container>
          <RightContentHeader title="" />

          <AdminPageInfoText
            title="  "
            subTitle="1   2   ."
            isNessasary={true}
          />
          <ProductDetail></ProductDetail>

          <AdminPageInfoText title="  " isNessasary={true} />
          <ProductPrimaryInfo></ProductPrimaryInfo>

          <AdminPageInfoText
            title=""
            subTitle="     ,        ."
            isNessasary={true}
          />
          <ProductPrice></ProductPrice>

          <AdminPageInfoText title="" subTitle="" isNessasary={true} />
          <Alert
            style={{ fontSize: "0.7rem", color: "#6d6d6d" }}
            variant="danger"
          >
            -      .<br></br>- 0
               .
          </Alert>
          <ProductStock></ProductStock>

          <AdminPageInfoText
            title="  "
            subTitle=""
            isNessasary={false}
          />
          <ProductThumnail></ProductThumnail>

          <AdminPageInfoText title="  " isNessasary={false} />
          <EditorBox style={{ width: "60%" }}></EditorBox>
        </Container>

        <ProductNoticeBanner></ProductNoticeBanner>
      </div>
    </>
  );
};

export default ProductUploadForm;
